var routeEnter = require('routeEnter');

module.exports = [
    Object.assign({
        path: "subscribe",
        breadcrumbName: "我订阅的资金",
        getComponent: function (location, cb) {
            require.ensure([], function (require) {
                cb(null, require("./components/subscribe.jsx"))
            }, 'projectSpaceSubscribe')
        }
    }, routeEnter),
    Object.assign({
        path: "subscribe/setting",
        breadcrumbName: "订阅设置",
        getComponent: function (location, cb) {
            require.ensure([], function (require) {
                cb(null, require("./components/setting.jsx"))
            }, 'projectSpaceSubscribeSetting')
        }
    }, routeEnter)
];


